import { FunctionalComponent, h } from '@stencil/core';

export type FcPictogramType = {
  name: string;
};

export const FcPictogram: FunctionalComponent<FcPictogramType> = props => {
  const theme = document.body.dataset.theme || 'core';
  const assetsPath = `assets/images/pictograms/${theme}/`;

  return !!props.name && <img src={`${assetsPath}${props.name}.svg`} alt=""></img>;
};
